// styled components
import { RankingAccordion, RankingSummary, RankingDetails } from "./style";

// components
import {
  CollectionCell,
  CryptoCell,
  FloorCell,
  ImageCell,
  TextCell,
} from "@constants/ranking";
import Pagination from "@ui/Pagination";

// hooks
import usePagination from "@hooks/usePagination";

// utils
import { memo, useEffect, useState } from "react";



import axios from "axios";

const Accordion = ({ period, category, type }) => {
  const [collectionData, setCollectionData] = useState([]);
  const [continuation, setContinuation] = useState();
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    (async () => {
      try {
        let sortByVal = "";
        switch (period.value) {
          case "all":
            sortByVal = "allTimeVolume";
            break;
          case "year":
            sortByVal = "1DayVolume";
            break;
          case "month":
            sortByVal = "30DayVolume";
            break;
          case "week":
            sortByVal = "7DayVolume";
            break;
          default:
            sortByVal = "allTimeVolume";
            break;
        }

      const { data } = await axios.get(
        "https://api-goerli.reservoir.tools/collections/v5",
          {
            params: {
              includeTopBid: "true",
              sortBy: sortByVal,
            },
          }
        );
      setCollectionData(data.collections);
      setContinuation(data.continuation);
        } catch (error) {
          console.log(error);
        } finally {
          setLoading(false)
        }
    })();
  }, [period.value]);

  const viewAll = async () => {
    try {
      setLoading(true);
    const { data } = await axios.get(
      "https://api-goerli.reservoir.tools/collections/v5",
        {
          params: {
            includeTopBid: "true",
            sortBy: "allTimeVolume",
            continuation, // add continuation parameter
          },
        }
      );
    setCollectionData((prevState) => prevState.concat(data.collections));
    setContinuation(data.continuation);
    console.log("------->", data.continuation);
      } catch (error) {
        console.log(error);
      } finally {
        setLoading(false);
      }
  };

  const dataByCategory =
    category.value === "all" ? collectionData : collectionData;
  const pagination = usePagination(dataByCategory, 15);

  return (
    <div ref={pagination.containerRef}>
      <div className="d-flex flex-column g-15">
        {pagination.currentItems().map((item, index) => {
          const {volume, floorAsk, topBid, onSaleCount} = item;

          return (
            <RankingAccordion key={index}>
              <RankingSummary>
                <CollectionCell value={item}  />
                <i className="icon icon-angle-down" />
              </RankingSummary>
              <RankingDetails>
                <table>
                  <tbody>
                    <tr>
                      <td className="property text-bold text-light">Volume</td>
                      <td className="value">
                        <CryptoCell value={volume} type={type} period={period} />
                      </td>
                    </tr>
                    <tr>
                      <td className="property text-bold text-light">Floor Price</td>
                      <td className="value">
                        <FloorCell value={floorAsk} type={type} />
                      </td>
                    </tr>
                    <tr>
                      <td className="property text-bold text-light">Top Bid</td>
                      <td className="value">
                      <FloorCell value={topBid} type={type} />
                      </td>
                    </tr>
                    <tr>
                      <td className="property text-bold text-light">
                      Items Listed
                      </td>
                      <td className="value"><TextCell value={onSaleCount} /></td>
                    </tr>
                  </tbody>
                </table>
              </RankingDetails>
            </RankingAccordion>
          );
        })}
      </div>

      {pagination.maxPage > 1 && (
        <Pagination pagination={pagination} caller={viewAll} />
      )}
    </div>
  );
};

export default memo(Accordion);
